import prisma from '@/lib/prisma';
import { NextRequest, NextResponse } from 'next/server';

export async function GET(
    req: NextRequest,
    props: { params: Promise<{ id: string }> }
) {
    const params = await props.params;
    const id = parseInt(params.id);

    if (isNaN(id)) {
        return new NextResponse('Invalid ID', { status: 400 });
    }

    try {
        const feature = await prisma.polaRuangRevisiRtrw.findUnique({
            where: {
                gid: id,
            },
        });

        if (!feature) {
            return new NextResponse('Feature not found', { status: 404 });
        }

        // Convert BigInt to string to ensure JSON serialization compatibility
        const featureWithStringBigInt = JSON.parse(
            JSON.stringify(feature, (key, value) =>
                typeof value === 'bigint' ? value.toString() : value
            )
        );

        return NextResponse.json(featureWithStringBigInt);
    } catch (error) {
        console.error('Error fetching feature details:', error);
        return NextResponse.json(
            { error: 'Internal Server Error' },
            { status: 500 }
        );
    }
}
